.App {
  background-color: rgb(75, 75, 75);
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(170, 170, 170);
}
.main-container > div:nth-child(odd) {
  background-color: rgba(57, 57, 57, 0.5);
}
.header-container {
  display: grid;
  grid-template-columns: 1fr 5fr;
}
.header-photo {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header-photo img {
  width: 150px;
  height: 150px;
  border-radius: 100px;
  border: 3px solid rgb(255, 166, 0);
  box-shadow: 0 0 7px #666;
}
.header-info {
  padding: 5%;
}
.header-name-first, .header-name-second {
  font-size: 42px;
  letter-spacing: 0.1em;
}
.header-name-first {
  color: rgb(220, 220, 220);
}
.header-name-second {
  color: rgb(255, 166, 0);
}
.header-subtitle {
  font-size: 0.9em;
  margin-top: 20px;
}
.header-contacts, .contacts-info {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* column-gap: 20px; */
  row-gap: 20px;
  justify-content: space-around;
}
.header-contacts-location, .header-contacts-phone, .header-contacts-email, .header-contacts-telegram, .header-contacts-linkedin {
  width: 200px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  align-items: flex-start;
}
.location-title, .email-title, .telegram-title, .linkedin-title {
  color:rgb(220, 220, 220);
}
.location-text a {
  color: rgb(170, 170, 170);
  text-decoration: none;
  font-size: 0.9em;
}
.telegram-text a {
  color: rgb(170, 170, 170);
  text-decoration: none;
  font-size: 0.9em;
}
.linkedin-text a {
  color: rgb(170, 170, 170);
  text-decoration: none;
  font-size: 0.9em;
}
.email-text a {
  color: rgb(170, 170, 170);
  text-decoration: none;
  font-size: 0.9em;
}
.location-text a:hover{
  color: rgb(255, 166, 0);
  transition: color 0.4s ease;
}
.telegram-text a:hover{
  color: rgb(255, 166, 0);
  transition: color 0.4s ease;
}
.email-text a:hover {
  color: rgb(255, 166, 0);
  transition: color 0.4s ease;
}
.linkedin-text a:hover {
  color: rgb(255, 166, 0);
  transition: color 0.4s ease;
}

.intro-container {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 5fr;
}
.intro-content {
  padding: 5%;
}
.intro-leftside-title, .education-leftside-title, .education-timeline, .experience-leftside, .skills-leftside, .portfolio-leftside, .contacts-leftside {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.intro-title-first, .education-title-first, .description-university, .date-span, .experience-title-first, .skills-title-first, .portfolio-title-first, .contacts-title-first {
  color: rgb(220, 220, 220);
}
.intro-content-paragraph {
  line-height: 1.5em;
}
.a-rsschool {
  color: rgb(220, 220, 220);
  text-decoration: none;
}
.a-rsschool:hover {
  color: rgb(255, 166, 0);
  transition: color 0.4s ease;
}

.education-container, .experience-container, .skills-container, .portfolio-container, .contacts-container {
  padding-top: 50px;
  padding-bottom: 50px;
  display: grid;
  grid-template-columns: 1fr 5fr;
}
.certificate-link {
  text-decoration: none;
  color: rgb(255, 166, 0);
}
.certificate-link:hover {
  color: rgb(220, 220, 220);
  transition: color 0.4s ease;
}
.education-timeline, .experience-timeline {
  padding: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}
.events {
  position: relative;
  display: grid;
  grid-template-columns: 16px 1fr;
  row-gap: 16px;
  padding-top: 32px;
  width: fit-content;
}
.event {
  display: grid;
  grid-template-columns: 16px 1fr;
  column-gap: 16px;
  grid-column: 1 / 3;
}
/* .description > p {
  margin: 0;
} */
.knob {
  grid-column: 1 / 2;
  align-self: center;
  width: 100%;
  aspect-ratio: 1 / 1; 
  z-index: 1;
  background: rgb(255, 166, 0);
  border-radius: 50%;
}
.date,
.description {
  grid-column: 2 / 3;
  align-self: center;
}
.line {
  position: absolute;
  grid-column: 1 / 2;
  justify-self: center;
  height: 100%;
  width: 4px;
  z-index: 0;
  background-color:rgba(220, 220, 220, 0.084);
}

.skills-info {
  padding: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  column-gap: 5%;
}
.skill-container {
  width: 100%;
  background-color: rgb(220, 220, 220);
}
.skill {
  text-align: right;
  padding: 5px 10px 5px 0;
  color: rgba(57, 57, 57, 0.7);
}
.skill-name {
  color: rgb(220, 220, 220);
}
.skill-html-percent {width: 90%; background-color: rgb(255, 166, 0);}
.skill-css-percent {width: 80%; background-color: rgb(255, 166, 0);}
.skill-js-percent {width: 80%; background-color: rgb(255, 166, 0);}
.skill-ts-percent {width: 80%; background-color: rgb(255, 166, 0);}
.skill-react-percent {width: 70%; background-color: rgb(255, 166, 0);}
.skill-git-percent {width: 70%; background-color: rgb(255, 166, 0);}
.skill-node-percent {width: 30%; background-color: rgb(255, 166, 0);}
.skill-testing-percent {width: 30%; background-color: rgb(255, 166, 0);}

.portfolio-info {
  padding: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  column-gap: 5%;
  row-gap: 5%;
}
.portfolio-card {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.portfolio-card img {
  border-radius: 0.5em;
  width: 100%;
  height: 100%;
  justify-self: center;
  z-index: 1;
}
.portfolio-card:hover {
  -webkit-box-shadow: 4px 4px 30px 15px rgba(255, 166, 0, 0.2);
  -moz-box-shadow: 4px 4px 30px 15px rgb(255, 166, 0, 0.2);
  box-shadow: 4px 4px 30px 15px rgb(255, 166, 0, 0.2);
  border-radius: 0.5em;
}
.overlay {
  width: 100%;
  height: 70%;
  padding-top: auto;
  padding-bottom: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: 0;
  transition: opacity 0.6s ease;
  position: absolute;
  bottom: 0;
  background-color: rgba(5, 5, 5, 0.8);
  border-radius: 30% 0 0.5em 0.5em;
  z-index: 1;
}
.overlay a {
  color: rgb(255, 166, 0);
  text-decoration: none;
}
.overlay a:hover {
  color: rgb(255, 255, 255);
  transition: color 0.4s ease;
}
.overlay p {
  letter-spacing: 0.05em;
  display: block;
  color: rgb(255, 255, 255);
  padding-left: 10px;
  padding-right: 10px;
}
.contacts-info-wrapper {
  padding-left: 5%;
  padding-right: 5%;
}
.contacts-info {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* column-gap: 20px; */
  row-gap: 20px;
  justify-content: flex-start;
}

@media screen and (max-width: 960px) {
  .header-container {
    grid-template-columns: 1fr 2fr;
  }
  .header-photo {
    justify-content: center;
  }
  .intro-container, .education-container, .experience-container, .skills-container, .portfolio-container, .contacts-container {
    grid-template-columns: 1fr;
  }
  .intro-leftside-title, .education-leftside-title, .education-timeline, .experience-leftside, .skills-leftside, .portfolio-leftside, .contacts-leftside {
    align-items: flex-start;
    padding-left: 5%;
  }
  /* .portfolio-container {
    height: 1600px;
  } */
  .portfolio-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 1300px;
    row-gap: 2%;
  }
  .portfolio-card {
    width: 400px;
  }

  @media screen and (max-width: 690px) {
    .header-container {
      display: block;
    }
    .header-photo {
      position: absolute;
      right: 5%;
      top: 30px;
    }
    .header-photo img {
      width: 120px;
      height: 120px;
    }

    @media screen and (max-width: 500px) {
      .header-photo img {
        width: 110px;
        height: 110px;
      }
      .skills-info {
        display: block;
      }
    }

    @media screen and (max-width: 470px) {
      .portfolio-info {
        height: 1000px;
      }
      .portfolio-card {
        width: 300px;
      }
      .overlay p {
        font-size: 0.9em;
        padding-left: 5px;
        padding-right: 5px;
      }

      @media screen and (max-width: 444px) {
        .header-contacts {
          justify-content: flex-start;
        }
      }
    }
  }
}






